import { buildInputFile, execute, loadImageElement } from 'wasm-imagemagick';
import { rejects } from 'assert';

export default class ImageProcessor {

    constructor(param) {

    }

    initialise() {
    }

    /**
     * Capture and Crop and Area of the Screen
     * @param {object} areaToCapture 
     * @param {object} result 
     */
    async captureAreaAsimage(areaToCapture, result) {
        return new Promise((resolve, reject) => {

            chrome.tabs.captureVisibleTab(null, { format: "png", quality: 100 }, async function (dataUrl) {

                
                let result = await this.improveImage(areaToCapture,dataUrl);
                resolve(result);

            }.bind(this));
        });
    }

    /**
     * 
     * @param {*} imageBlob 
     */
    async blobToDataURL(imageBlob) {

        return new Promise((resolve, reject) => {

            var reader = new FileReader();
            reader.readAsDataURL(imageBlob);
            reader.onloadend = function () {
                
                    var processedDataUrl = "data:image/png;base64," + reader.result.replace(/^data:.+;base64,/, '');
                    resolve(processedDataUrl);
                
            }

        })
    }

    /**
     * Improve and Crop image to size
     * @param {object} areaToCapture 
     * @param {string} dataUrl 
     */
    async improveImage(areaToCapture, dataUrl) {

        //Device pixel ratio 
        let deviceRatio = areaToCapture.ratio;
        
        // Subtract the Page scroll from the x and y positions
        let imgY = (areaToCapture.y - areaToCapture.pageYOffset) * deviceRatio;
        let imgX = (areaToCapture.x - areaToCapture.pageXOffset) * deviceRatio;

        let cropString = areaToCapture.width * deviceRatio + 'x' + areaToCapture.height * deviceRatio + '+'
            + imgX + '+' + imgY;
        const { outputFiles, exitCode } = await execute({
            inputFiles: [await buildInputFile(dataUrl, 'image1.png')],
            commands: [
                //         'convert image1.png -rotate 70 image2.png',
                // heads up: the next command uses 'image2.gif' which was the output of previous command:
                'convert -crop ' + cropString + ' image1.png image2.png',
                'convert image2.png -adaptive-resize 100% -colorspace Lab -units PixelsPerInch -density 300  image3.png',
                'convert image3.png -unsharp 0.5x0.5+0.5+0.008 image4.png',
                'convert -units PixelsPerInch -density 300 -resize 200% -colorspace gray -type grayscale -contrast-stretch 0 -filter spline -unsharp 0x1 image2.png image3.png',
                'convert image4.png -write MPR:source ' +
                '-morphology close rectangle:1x2 ' +
                '-morphology erode square MPR:source -compose Lighten -composite ' +
                '-morphology erode square MPR:source -composite ' +
                '-morphology erode square MPR:source -composite ' +
                '-morphology erode square MPR:source -composite ' +
                '-morphology erode square MPR:source -composite ' +
                '-morphology erode square MPR:source -composite ' +
                '-morphology erode square MPR:source -composite ' +
                '-morphology erode square MPR:source -composite ' +
                '-morphology erode square MPR:source -composite ' +
                'image5.png'


                //  'convert image2.png -set colorspace Gray -separate -average image3.png'
                //      'convert image1.png -fuzz 50% -fill black -opaque black -bordercolor white -border 2 -fill black -draw "color 0,0 floodfill" -alpha off -negate -units pixelsperinch -density 300 image2.png'
            ],
        })
        if (exitCode !== 0) {
            await loadImageElement(outputFiles[0], document.getElementById('outputImage'));


        }

        let imageToOCR = await this.blobToDataURL(outputFiles[outputFiles.length - 1].blob);
        let imageToDisplay = await this.blobToDataURL(outputFiles[0].blob);

        // returnProcessedImage
        return {
            imageToOCR: imageToOCR,
            imageToDisplay: imageToDisplay,
            imageFullScreen: dataUrl
        };
    }
}

const ImageProcessorInstance = new ImageProcessor();
ImageProcessorInstance.initialise();