import SpeechPubSub from './SpeechPubSub.js';
import SpeechManager from './extension-speechmanager.js';
import OCRProcessor from './OCRProcessor.js';
import ImageProcessor from './ImageProcessor.js';

class SSRBackground {

    constructor(param) {

    }

    initialise() {


        //On Browser Action 
        chrome.browserAction.onClicked.addListener(this.onBrowserAction.bind(this));

        //On Message 
        chrome.runtime.onMessage.addListener(this.onMessage.bind(this));

        //On external Message
        chrome.runtime.onMessageExternal.addListener(this.onMessage.bind(this));

        this._imageProcessor = new ImageProcessor();
        this._ocrProcessor = new OCRProcessor();

        //=== PubSub ======
        this.speechEvents = SpeechPubSub();

        this._speechManager = null;

        this._speechManager = new SpeechManager();
        this._speechManager.initialise(this.speechEvents);

        this.speechEvents.sub(this._onPubSubEvent.bind(this));

        var parameters = {};
        parameters['method'] = 'getVoices';
        parameters['type'] = 'SP_M_1342_CK';

        this.ssrData = null;
        this.ocrRequest = null;
        this.regionIndex = 0;
        this.sentenceIndex = 0;

        //intialise the voice list and teseract with small image
        this.speechEvents.pub("getVoices", parameters);

    }

    onBrowserAction(tab) {

        var screenCaptureCommand = { "command": "TH_SSR_Start_Capture", voice: "Ava?", speed: "50" };

        chrome.tabs.getSelected(null, tab => {
            chrome.tabs.sendRequest(tab.id, screenCaptureCommand, function () { });
        });

    }

    /**
     * 
     * @param {object} request message request object 
     * @param {} sender sender / id of the sender of the request
     * @param {*} sendResponse loaction to send a responce 
     */
    onMessage(request, sender, sendResponse) {

        switch (request.command) {
            case "TH_rectToOCR_SSR":

                let ocrRequest = {
                    request: request,
                    sender: sender,
                    sendResponse: sendResponse
                }

                this.onCaptureRequest(ocrRequest);
                break;
            case "TH_start_SSR":
            case "startScreenShotReader":

                chrome.tabs.getSelected(null, function (tab) {
                    chrome.tabs.sendRequest(tab.id, request, function (response) {

                    });
                });

                break;
            case "TH_externalOCR_SSR":
            case "externalOCR":

                var externalID = sender.id;
                this.doOCR(request, externalID, sendResponse);
                break;

            case "TH_Stop_SSR":
            case "stop":
                this.stop();
                break;
            case "pause":

            this.pause();
            break;

            case "TH_Play_Again_SSR":
                this.playAgain();
                break;

            default:
        }
    }

    async doOCR(ocrRequest, externalID, sendResponse) {

        var image = ocrRequest.image;

        let result = {
            'imageToOCR': image
        };

        //Do OCR
        result = await this._ocrProcessor.ocrImage(result);
        var paragraphs = result.ocr.blocks[0].paragraphs;

        var formatedResult = [];
        formatedResult = this.convertOCRparagraphresult(paragraphs);
        var onOCRCommand = { "command": "onocr", "data": formatedResult };

        if (externalID == undefined) {
            sendResponse(onOCRCommand);
        }

        chrome.runtime.sendMessage(externalID.toString(), onOCRCommand);

    }

    /**
     * Request to Capture and OCR a area on screen
     */
    async onCaptureRequest(ocrRequest) {

        let result = {};


        //Get requested Image area
        result = await this._imageProcessor.captureAreaAsimage(ocrRequest.request.rectToOCR, result);

        //chrome.tabs.create({url: result.imageToDisplay, active: false});
        //chrome.tabs.create({url: result.imageFullScreen, active: false});

        //Do OCR
        result = await this._ocrProcessor.ocrImage(result);

        var words = result.ocr.words;

        var paragraphWords = result.ocr.blocks[0].paragraphs[0].words;
        var paragraphs = result.ocr.blocks[0].paragraphs;

        var formatedResult = [];

        formatedResult = this.convertOCRparagraphresult(paragraphs);

        var onOCRCommand = { "command": "TH_onOCR_SSR", 'text': result.ocr.text, "data": formatedResult, "image": result.imageToDisplay, "points": ocrRequest.request.rectToOCR, "orgin": ocrRequest };

        chrome.tabs.sendRequest(ocrRequest.sender.tab.id, onOCRCommand, function () { });

        this.regionIndex = 0;
        this.sentenceIndex = 0;

        this.ssrData = JSON.parse(formatedResult);
        this.ocrRequest = ocrRequest;
        this.play(this.ssrData[0][0].words, ocrRequest.request.rectToOCR.speechSettings.voice, ocrRequest.request.rectToOCR.speechSettings.speed);
    }

    /**
     * On Word message
     * @param {object} request  
     */
    onWord(request) {

        var onOCRCommand = {
            "command": "TH_onWord_SSR", "data": {
                word: request.word,
                regionIndex: this.regionIndex,
                sentenceIndex: this.sentenceIndex
            }
        };

        chrome.tabs.getSelected(null, function (tab) {
            chrome.tabs.sendRequest(tab.id, onOCRCommand, function () { });
        });
    }

    /**
     * On Stop event from the speech engine
     * @param {*} request 
     */
    onStop(request) {
        var onOCRCommand = {
            "command": "TH_onStop_SSR", "data": {}
        };

        chrome.tabs.getSelected(null, function (tab) {
            chrome.tabs.sendRequest(tab.id, onOCRCommand, function () { });
        });
    }

    /**
     * Play or Speak a text items
     * @param {*} text 
     * @param {string} voice 
     * @param {string} speed 
     */
    play(text, voice, speed) {
        var wordIndex = 0;
        var selection = [];
        for (wordIndex = 0; wordIndex < text.length; wordIndex++) {

            selection.push(text[wordIndex].text);
        }

        var parameters = {};
        parameters['method'] = 'play';
        parameters['type'] = 'SP_M_1342_CK';
        parameters['words'] = selection;
        parameters['origin'] = 'ca753d9d-f8c8-47dd-bd00-8033cd620939';
        parameters['voice'] = voice;
        parameters['speed'] = speed;

        this.speechEvents.pub("play", parameters);
    }

    /**
     * Message a stop event to the speech engine.
     */
    stop() {

        var parameters = {};
        parameters['method'] = 'stop';
        parameters['type'] = 'SP_M_1342_CK';
        parameters['origin'] = 'ca753d9d-f8c8-47dd-bd00-8033cd620939';

        this.speechEvents.pub("stop", parameters);
    }

    /**
     * Message a stop event to the speech engine.
     */
    pause() {

        var parameters = {};
        parameters['method'] = 'stop';
        parameters['type'] = 'SP_M_1342_CK';
        parameters['origin'] = 'ca753d9d-f8c8-47dd-bd00-8033cd620939';

        this.speechEvents.pub("pause", parameters);
    }

    /**
     * Play the the current passage again from the beginning.
     */
    playAgain() {
        this.regionIndex = 0;
        this.sentenceIndex = 0;
        this.play(this.ssrData[this.regionIndex][this.sentenceIndex].words, this.ocrRequest.request.rectToOCR.speechSettings.voice, this.ocrRequest.request.rectToOCR.speechSettings.speed);
    }

    /**
    * Determines if a value exists in an array.
    * @param {Array} array to check.
    * @param {string} valueToCheckFor the value to check for in the array.
    * @return {boolean} if the value exists in the array.
    */
    arrayContains(array, valueToCheckFor) {
        var i = array.length;
        // loop through the array checking each element for the value.
        while (i--) {
            if (array[i] === valueToCheckFor) {
                return true;
            }
        }
        return false;
    }

    /**
     * Encode Characters
     * @param {string} word 
     */
    encodeChars(word) {
        try {

            var charMap = {
                '8217': "'",
                '96': "'",
                '8482': ' ',
                '8212': ' '
            }

            var newWord = '';

            var charCode = '';
            for (var index = 0; index < word.length; index++) {
                charCode = word.charCodeAt(index).toString();
                if (charMap[charCode] === undefined) {
                    newWord += word.charAt(index);
                }
                else {
                    newWord += charMap[charCode];
                }
            }

            return newWord;
        }
        catch (err) {
            console.log(err);
        }
    }

    /**
     * End or Sentence Check
     * @param {string} stringToCheck 
     */
    checkIfEndOfSentence(stringToCheck) {
        try {

            var sentExclusions = [
                'Dr.',
                'Mr.',
                'Mrs.',
                'i.e.',
                'e.g.',
                'St.',
                'N.B.',
                'NB.',
                'Ltd.',
                'plc.',
                'U.S.',
                'a.m.',
                'p.m.',
                'ext.',
                'vs.',
                'Jan.',
                'Feb.',
                'Mar.',
                'Apr.',
                'Jun.',
                'Jul.',
                'Aug.',
                'Sep.',
                'Oct.',
                'Nov.',
                'Dec.',
                'N.E.',
                'S.E.',
                'S.W.',
                'N.W.'
            ];


            // if any of the sentence exlusions return not a sentnece break
            if (this.arrayContains(sentExclusions, stringToCheck.trim())) {
                return false;
            }


            if (stringToCheck.trim().search('[\.?!]$') == -1) {
                return false;
            }

            return true;

        }
        catch (err) {
            console.log(err);
        }
    }

    _onPubSubEvent(target, parameter) {
        try {
            if (target !== undefined) {

                if (target == 'onthAPIWord') {
                    this.onWord(parameter);
                }
                if (target == 'onthAPIWordonthAPIStop') {

                    if (this.sentenceIndex < this.ssrData[this.regionIndex].length - 1) {
                        this.sentenceIndex++;

                        this.play(this.ssrData[this.regionIndex][this.sentenceIndex].words, this.ocrRequest.request.rectToOCR.speechSettings.voice, this.ocrRequest.request.rectToOCR.speechSettings.speed);
                        return;
                    }

                    if (this.regionIndex < this.ssrData.length - 1) {
                        this.sentenceIndex = 0;
                        this.regionIndex++;
                        return;
                    }

                    this.onStop(parameter);
                }

            }

        } catch (err) {
            console.log(err);
        }
    }

    /**
     * Convert OCR response
     * @param {object} OCRResult 
     */
    convertOCRparagraphresult(OCRResult) {

        var blockIndex = 0;
        var lineIndex = 0;
        var wordIndex = 0;
        var nextWordIndex = 0;

        var lineTop = 0;
        var lineBottom = 0;

        var region = null;
        var line = null;
        var word = null;

        var newLine = null;
        var newSentence = {};
        newSentence.words = [];
        newSentence.bookmarkedText = '';


        var debugLine = "";
        var pattern = /\(?[A-Z][^\.]+[\.!\?]\)?/g, match;
        var bookMarkSentence = "";
        var bookmarkIndex = 0;
        var ocrScale = 2;

        var parsedResult = [];

        parsedResult.push(new Object()); // add region/paragraph object. 
        parsedResult[blockIndex] = [];

        // lines are every other object. Label inbetween
        for (lineIndex = 0; lineIndex < OCRResult.length; lineIndex++) {
            line = OCRResult[lineIndex];

            var index;
            var tempWords = [];

            for (index = 0; index < OCRResult[lineIndex].lines.length; ++index) {
                tempWords.push(OCRResult[lineIndex].lines[index].words);
                //console.log(OCRResult[lineIndex].lines[index]);
            }

            tempWords = tempWords.reduce(function (a, b) {
                return a.concat(b);
            }, []);

            for (wordIndex = 0; wordIndex < tempWords.length; wordIndex++) {
                word = new Object();

                lineTop = tempWords[wordIndex].bbox.y0;
                lineBottom = tempWords[wordIndex].bbox.x0;

                var rect = new Object();
                rect.bottom = (lineBottom);
                rect.top = (lineTop);

                rect.left = (tempWords[wordIndex].bbox.x1 - tempWords[wordIndex].bbox.x0);
                rect.right = (tempWords[wordIndex].bbox.y1 - tempWords[wordIndex].bbox.y0);

                word.text = tempWords[wordIndex].text;
                word.rects = [];

                word.rects.push(rect);

                newSentence.words.push(word);

                if (wordIndex == 0 && newSentence.length > 0) {
                    debugLine += ' ';
                }
                debugLine += word.text;

                newSentence.bookmarkedText += "<bookmark mark='" + bookmarkIndex + "'/>" + this.encodeChars(word.text);
                bookmarkIndex++;


                if (wordIndex == tempWords.length - 1) {

                    // if (pattern.exec(debugLine) !== null) {
                    //       if (word.text.split(/(.*?[.?!])/g).length > 1){
                    newSentence.bookmarkedText += "<bookmark mark='" + bookmarkIndex + "'/>";
                    parsedResult[blockIndex].push(newSentence);
                    //                     console.log(debugLine);
                    debugLine = '';
                    newSentence = {};
                    newSentence.words = [];
                    newSentence.bookmarkedText = '';
                    bookmarkIndex = 0;
                }

                if (this.checkIfEndOfSentence(word.text)) {
                    // if (pattern.exec(debugLine) !== null) {
                    //       if (word.text.split(/(.*?[.?!])/g).length > 1){
                    newSentence.bookmarkedText += "<bookmark mark='" + bookmarkIndex + "'/>";
                    parsedResult[blockIndex].push(newSentence);
                    //                     console.log(debugLine);
                    debugLine = '';
                    newSentence = {};
                    newSentence.words = [];
                    newSentence.bookmarkedText = '';
                    bookmarkIndex = 0;

                }
                //       }
            }
        }


        if (newSentence.words.length > 0) {
            newSentence.bookmarkedText += "<bookmark mark='" + bookmarkIndex + "'/>";
            parsedResult[blockIndex].push(newSentence);
            //          console.log(debugLine);
            debugLine = '';
            newSentence = {};
            newSentence.words = [];
            newSentence.bookmarkedText = '';
            bookmarkIndex = 0;

        }

        return JSON.stringify(parsedResult);

    }
}
const SSRBackgroundInstance = new SSRBackground();
SSRBackgroundInstance.initialise();