import  { TesseractWorker } from 'tesseract.js';

const worker = new TesseractWorker({
    corePath: chrome.extension.getURL("tesseract-core.js"),
    workerPath: chrome.extension.getURL("worker.js"),
});

export default class OCRProcessor {

    constructor(param) {}

    initialise() {}

    /**
    * OCR Image 
    * @param {object} ocrRequest Request containing a dataURL
    */
    async ocrImage(ocrRequest) {
        return new Promise((resolve, reject) => {

            worker.recognize(ocrRequest.imageToOCR).progress(function (message) {
                //console.log(message) 
            }).then(function (result) {
                //Return the request appending the result
                ocrRequest.ocr = result;
                resolve(ocrRequest);
                console.log(ocrRequest)
            });

        });
    }

}

const OCRProcessorInstance = new OCRProcessor();
//OCRProcessorInstance.initialise();

